$dark-blue: #202125;
$dark-salt: #191d1c;
$grayish-blue: #434343;
$white: #ffffff;
$soft-blue: #8ab4f8;
$soft-blue2: #5383ed;
$silver: rgba(255, 255, 255, .5);
$soft-yellow: #f7d775;
$bright-orange: #edae3b;
$bright-red: #FE2E2E;
$lime-green: #4fa19d;


.wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0 3vh 5vh 3vh;
  .container {
    display: flex;
    flex-direction: row;
    box-shadow: 0 5px 5px 1px $silver;
    background: $white;
    text-align: center;
    border-radius: 10px;
    overflow: hidden;
    margin: 1em auto;
    min-height: 350px;
    max-width: 600px;
    width: 100%;
    .block {
      padding-top: 20vh;
      width: 90%;
      max-width: 1300px;
      margin: 0 auto;
      text-align: center;
      h2 {
        text-align: center;
        font-weight: 700;
        font-size: 58px;
      }
    }
    .wrapBlock {
      max-height: 100px;
      overflow: hidden;
      order: 0;
      margin-bottom: 10px;
    }
    .checkbox {
      display: none;
    }
    .checkbox:checked + .wrapBlock {
      height: auto;
    }
    .link {
      text-decoration: none;
      display: block;
      margin-bottom: 15px;
      &:hover {
        text-decoration: none;
      }
    }
    label {
      order: 1;
      color: $soft-blue2;
      font-size: 18px;
      cursor: pointer;
      font-weight: bold;
      font-style: italic;
    }
    .productDetails {
      width: 50%;
      background: $soft-blue;
      will-change: transform;
      &::after {
        content: "";
        position: absolute;
        top: -2px;
        left: -2px;
        background: linear-gradient(30deg, $soft-yellow, $soft-blue2, $bright-red, $lime-green,  $soft-yellow, $soft-blue2, $bright-red, $lime-green);
        background-size: 300% 300%;
        width: calc(100% + 3px);
        height: calc(100% + 3px);
        z-index: -1;
      }
      h4 {
        margin: 0 auto;
        padding-top: 20px;
        padding-bottom: 15px;
        height: 12vh;
        width: 95%;
        font-size: 24px;
        color: $dark-salt;
        overflow: scroll;
      }
      p {
        margin: 0 10px 0 10px;
        color: $dark-salt;
        font-size: 18px;
        width: 95%;
        overflow: hidden;
        height: 25vh;
      }
      .more {
        padding: 5px;
        border-radius: 10px;
        font-size: 18px;
        width: 75px;
        background: $dark-salt;
        box-shadow: inset 2px 2px 2px hsl(0deg 0% 100% / 25%), inset -2px -2px 2px rgb(0 0 0 / 25%);
        display: flex;
        align-items: center;
        text-align: center;
        margin: 0 auto;
        position: absolute;
        left: 24%;
        top: 60%;
        svg {
          fill: $white;
          width: 20px;
          height: 20px;
          margin: 0 auto;
        }
      }
    }
    .productImage {
      transition: all 0.5s ease-out;
      display: inline-block;
      position: relative;
      overflow: hidden;
      height: 100%;
      float: right;
      width: 50%;
      img {
        width: 100%;
        height: 100%;
        transition: all 0.5s ease-out;
        cursor: pointer;
        &:hover {
          transform: scale(1.5, 1.5);
        }
      }
      &::after {
        content: "";
        position: absolute;
        top: -2px;
        left: -2px;
        background: linear-gradient(30deg, $soft-yellow, $soft-blue2, $bright-red, $lime-green,  $soft-yellow, $soft-blue2, $bright-red, $lime-green);
        background-size: 300% 300%;
        width: calc(100% + 3px);
        height: calc(100% + 3px);
        z-index: -1;
      }
    }
    .productImage:hover .info {
      transform: translateX(0);
    }
    .control {
      position: absolute;
      left: 25%;
    }
    .btn {
      transform: translateY(0px);
      transition: 0.3s linear;
      background: $lime-green;
      border-radius: 10px;
      position: relative;
      top: 0;
      overflow: hidden;
      cursor: pointer;
      outline: none;
      border: none;
      color: $white;
      padding: 0;
      margin-bottom: 20px;
      &:hover {
        transform: translateY(-4px);
      }
      span {
        transition: transform 0.3s;
        display: inline-block;
        padding: 10px 20px;
        font-size: 1.2em;
        margin:0;
      }
    }
    .btn .price, .shoppingCart{
      background: $dark-salt;
      border: 0;
      margin: 0;
    }
    .btn .price {
      transform: translateX(-10%);
      padding-right: 10px;
    }
    .btn .shoppingCart {
      transform: translateX(-100%);
      position: absolute;
      background: $dark-salt;
      z-index: 1;
      left: 0;
      top: 0;
    }
    .btn .buy {
      z-index: 3;
      font-weight: bolder;
    }
    .btn:hover .price {
      transform: translateX(-110%);
    }
    .btn:hover .shoppingCart {
      transform: translateX(0%);
    }
  }
  .supportingBlock {
    display: flex;
    justify-content: space-between;
    width: 700px;
  }
}
@media screen and (max-width: 720px) {
  .wrapper {
    .container {
      display: flex;
      flex-direction: column;
      .productImage {
        width: 100%;
        min-height: 65%;
        order: -1;
      }
      .productDetails {
        width: 100%;
        min-height: 300px;
        padding-bottom: 30px;
        .more {
          left: 34%;
        }
      }
      .control {
        left: 35%;
      }
    }
    .btn {
      top: -30px;
    }
  }
}
@media screen and (max-width: 500px) {
  .container {
    .productImage {
      width: 100%;
      min-height: 58%;
    }
    .productDetails {
      .more {
        left: 29%;
      }
    }
    .control {
      left: 30%;
    }
  }
}

